import { createSlice } from '@reduxjs/toolkit';

import {
    UserInterface
} from "./interface";


import axios from "axios";

const initialState:any = {
    username:"",
    email: "",
    isValidEmail: false,
    password: "",
    isValidPassword: false,
    isAutheticated: false,
};

const authSlice = createSlice({
    name: 'auth',
    initialState: initialState,
    reducers: {
        SET_EMAIL: (state, action) => {
            return {
                ...state,
                email: action.payload.email,
            };
        },
        SET_EMAIL_VALID: (state) => {
            return {
                ...state,
                isValidEmail: true,
            };
        },
        SET_EMAIL_NOT_VALID: (state) => {
            return {
                ...state,
                isValidEmail: false,
            };
        },
        SET_PASSWORD: (state, action) => {
            return {
                ...state,
                password: action.payload.password,
            };
        },
        SET_PASSWORD_VALID: (state) => {
            return {
                ...state,
                isValidPassword: true,
            };
        },
        SET_PASSWORD_NOT_VALID: (state) => {
            return {
                ...state,
                isValidPassword: false,
            };
        },
    }
});

export const USER_SIGN_IN = (userData:any)=>{
    return (dispatch:any, getState:any)=>{
        axios.post("", {email: userData.email, password: userData.password})
        .then((res)=>{
            if(res.status === 200){
                const data:any = res.data;
                if(data.success === true){
                    
                }
            }
        })
        .catch((e)=>{
            console.log(e);
        });
    };
};

export const { 
    SET_EMAIL,
    SET_PASSWORD,
    SET_EMAIL_VALID,
    SET_EMAIL_NOT_VALID,
    SET_PASSWORD_VALID,
    SET_PASSWORD_NOT_VALID,
} = authSlice.actions

export default authSlice.reducer